*{
    margin: 0;
    padding: 0;
}
#root{
    margin: 0;
    color: aliceblue;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
span{
    font-size: 1.1em;
}
img{
    position: fixed;
    top: 0;
    z-index: 0;
    object-fit: contain;
    height: 4in;
    width: 100vw;
    margin-bottom: 10px;
    background-color: rgb(44, 44, 44);
}
.body{
    background-color: rgb(35, 35, 35);
    position: relative;
    margin-top: 4in;
    overflow: scroll;
    height: calc(100vh - 70px);
    z-index: 1;
}
h2{
    padding: 5px 15px;
    margin-top: 25px;
}
p{
    padding: 0 10px;
    margin-bottom: 15px;
}
.email{
    width: 80vw;
    margin: 15px 10vw 0 10vw;
    font-size: larger;
    background-color: aliceblue;
}
main{
    width: 100vw;
    text-align: center;
}
button{
    height: 50px;
    width: 50px;
    background-color: rgba(255, 255, 255, 0.137);
    border: none;
    box-shadow: 0 5px 5px;
    border-radius: 10px;
}
footer{
    position: fixed;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: rgb(35, 35, 35);
    border-top: 1px solid rgba(255, 255, 255, 0.174);
    bottom: 0;
    width: 100vw;
    height: 70px;
    z-index: 2;
}